import {
	keyOneuseList,
	keyOneUseApprove
} from "../../api/keycabint.js"

var timer = null;
export default {
	data() {
		return {
			applylist: [],
			showButton: [],
			buttonId: '',
			loadingText: '加载中...',
			start: 0,
			isFnished: false
		}
	},
	onLoad() {
		this.auditList();
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.start = 0;
		this.applylist = [];
		this.auditList();
		uni.stopPullDownRefresh();
	},
	onReachBottom: function() {
		const that = this;
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			(!that.isFnished) && that.auditList();
		}, 1000);
	},
	methods: {
		auditList() {
			var that = this;
			this.loadingText = '加载中...';
			
			keyOneuseList(this.start,10).then(res => {
				this.loadingText = '';
				console.log(res.data.applylist)

				this.start = this.start + 10;
				uni.hideNavigationBarLoading();
				this.applylist = this.applylist.concat(res.data.applylist);
				if (this.applylist == '') {
					uni.hideNavigationBarLoading();
					this.loadingText = '暂无数据';
					return false;
				}
				if (this.applylist.length < 10) {
					that.isFnished = true;
				}
			})
		},
		applyRefuse(logid) {
			const that = this;
			uni.showModal({
				content: '是否确认拒绝？',
				confirmText: "确认",
				confirmColor: "#0067C6",
				success: function(res) {
					if (res.error != 0) {
						uni.showToast({
							icon: 'none',
							title: res.msg
						});
					}
					if (res.confirm) {
						keyOneUseApprove(logid,0).then(res => {
							that.showButton.push(logid);
							uni.showToast({
								icon: 'none',
								title: res.msg
							});
						})
					} else if (res.cancel) {

					}
				}
			});

		},
		applyPast(logid) {
			const that = this;
			uni.showModal({
				content: '是否确认通过？',
				confirmText: "确认",
				confirmColor: "#0067C6",
				success: function(res) {
					if (res.error != 0) {
						uni.showToast({
							icon: 'none',
							title: res.msg
						});
					}
					if (res.confirm) {
						keyOneUseApprove(logid,1).then(res => {
							that.showButton.push(logid);
							uni.showToast({
								icon: 'none',
								title: res.msg
							});
						})
					} else if (res.cancel) {

					}
				}
			});
		}
	}
}
